:root {
  --bg-primary: #fdfaf6;
  --bg-secondary: #f5ede6;
  --bg-card: #fdfaf6;
  --bg-elevated: #ffffff;
  --border-subtle: rgba(122, 99, 84, 0.08);
  --border-accent: rgba(122, 99, 84, 0.15);
  --text-primary: #3d2c23;
  --text-secondary: #7a6354;
  --text-tertiary: #a89485;
  --accent: #d4956a;
  --accent-soft: rgba(212, 149, 106, 0.12);
  --accent-glow: rgba(212, 149, 106, 0.3);
  --user-bubble: rgba(212, 149, 106, 0.14);
  --ai-bubble: rgba(122, 99, 84, 0.04);
  --success: #8bc99a;
  --success-glow: rgba(139, 201, 154, 0.4);
  --error: #e07a6a;
  --error-glow: rgba(224, 122, 106, 0.4);
}
html,
body {
  margin: 0;
  padding: 0;
  background: var(--bg-primary);
  color: var(--text-primary);
  font-family: "Inter", system-ui, -apple-system, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body::before {
  content: "";
  position: fixed;
  inset: 0;
  background: radial-gradient(ellipse 60% 50% at 50% 0%, rgba(212, 149, 106, 0.04), transparent 70%);
  pointer-events: none;
  z-index: 0;
}
::-webkit-scrollbar {
  width: 4px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: var(--border-accent);
  border-radius: 2px;
}
@keyframes pulse-ring {
  0% {
    transform: scale(1);
    opacity: 0.5;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
@keyframes pulse-ring-2 {
  0% {
    transform: scale(1);
    opacity: 0.35;
  }
  100% {
    transform: scale(2.4);
    opacity: 0;
  }
}
@keyframes pulse-ring-3 {
  0% {
    transform: scale(1);
    opacity: 0.2;
  }
  100% {
    transform: scale(2.8);
    opacity: 0;
  }
}
@keyframes breathe {
  0%,
  100% {
    box-shadow: 0 0 24px var(--accent-glow), 0 0 60px rgba(212, 149, 106, 0.08);
  }
  50% {
    box-shadow: 0 0 40px var(--accent-glow), 0 0 90px rgba(212, 149, 106, 0.15);
  }
}
@keyframes bounce-dot {
  0%,
  80%,
  100% {
    transform: translateY(0);
    opacity: 0.4;
  }
  40% {
    transform: translateY(-5px);
    opacity: 1;
  }
}
@keyframes wave-bar {
  0%,
  100% {
    height: 6px;
  }
  50% {
    height: 20px;
  }
}
@keyframes fade-in-up {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes glow-pulse {
  0%,
  100% {
    box-shadow: 0 0 4px currentColor, 0 0 8px currentColor;
  }
  50% {
    box-shadow: 0 0 8px currentColor, 0 0 16px currentColor, 0 0 24px currentColor;
  }
}
.animate-pulse-ring {
  animation: pulse-ring 1.8s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}
.animate-pulse-ring-2 {
  animation: pulse-ring-2 1.8s cubic-bezier(0.4, 0, 0.6, 1) infinite 0.5s;
}
.animate-pulse-ring-3 {
  animation: pulse-ring-3 2.2s cubic-bezier(0.4, 0, 0.6, 1) infinite 1s;
}
.animate-breathe {
  animation: breathe 3s ease-in-out infinite;
}
.animate-fade-in-up {
  animation: fade-in-up 0.3s ease-out forwards;
}
.animate-glow-pulse {
  animation: glow-pulse 2s ease-in-out infinite;
}
.wave-bar {
  animation: wave-bar 0.9s ease-in-out infinite;
}
.wave-bar:nth-child(2) {
  animation-delay: 0.1s;
}
.wave-bar:nth-child(3) {
  animation-delay: 0.2s;
}
.wave-bar:nth-child(4) {
  animation-delay: 0.3s;
}
.wave-bar:nth-child(5) {
  animation-delay: 0.15s;
}
.bounce-dot {
  animation: bounce-dot 1.4s ease-in-out infinite;
}
.bounce-dot:nth-child(2) {
  animation-delay: 0.2s;
}
.bounce-dot:nth-child(3) {
  animation-delay: 0.4s;
}
